@import url(https://fonts.googleapis.com/css2?family=Commissioner:wght@400;500;700&display=swap);
*{margin:0px;padding:0px;font-family:'Commissioner', sans-serif}p,span,li,a{font-size:16px}li{list-style-type:none}.stats{display:flex}.btn{cursor:pointer;font-weight:bold;color:#7a7a7a;border:none;border-radius:30px;padding-left:30px;padding-top:15px;padding-bottom:15px;padding-right:30px;margin-left:5px;margin-top:5px;margin-bottom:5px}.btn:hover{box-shadow:5px 5px 10px #7a7a7a}.btn.btn-primary{background-color:#147b74;color:white}.check{display:flex;align-items:center;background-color:#EFEFEF;cursor:pointer;font-weight:bold;color:#7a7a7a;border-radius:30px;margin:5px 5px 5px}.check>span.icon{display:flex;align-items:center}.check>span.label{padding-left:10px;padding-right:35px}.check:hover{box-shadow:5px 5px 10px #7a7a7a}input[type="checkbox"]{display:none;opacity:0.0}input[type="checkbox"]:checked+label>span{color:#147b74}input[type="checkbox"]:checked+label>span>svg.icon-bookmark>g>circle{fill:#147b74}input[type="checkbox"]:checked+label>span>svg.icon-bookmark>g>path{fill:#fff}

